.user_test {
    width: 100%;
    height: 100vh;
    padding-top: 20px;
    padding-bottom: 20px;

    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__back {
        align-self: flex-start; } }

.user_wr {
    height: 100%;

    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;

    &__title {
        font-size: 35px; } }

.user_sex {
    &__body {
        display: flex;
        flex-direction: column;

        button {
            &:first-child {
                margin-bottom: 15px; } } }

    &__btn_active {
        background-color: #bababa61; }

    &__btn_disabled {
        opacity: .5; } }

.user_age {
    &__body {
        height: 50%; } }


.user_half {
    width: 100%;
    &__body {
        width: 100%; } }

.user_activite {
    &__body {
        display: flex;
        flex-direction: column; }

    &__button {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px; }

        &_active {
            background-color: #bababa61; } } }


.user_total {
    width: 100%;

    &__body {
        width: 100%;
        padding: 0 10%;

        box-sizing: border-box; }
    &__row {
        display: flex;
        justify-content: space-between;

        padding: 15px 0;

        border-bottom: 1px solid $c-gray-light; }

    &__txt {
        font-weight: 500; }

    &__txt_color {
        color: $c-success; } }


.user_diet {
    &__mg {
        font-weight: bold; }
    &__name {
        position: relative;
        padding-left: 15px;
        &::before {
            content: "";

            width: 8px;
            height: 8px;

            background-color: red;
            position: absolute;
            left: 0;
            top: 50%;
            border-radius: 50%;
            transform: translateY(-50%); }

        &_protein {
            &::before {
                background-color: #e64d4d; } }
        &_fat {
            &::before {
                background-color: #ffbe7b; } }
        &_carb {
            &::before {
                background-color: #fff176; } } }

    &__table {
        &_row {
            display: flex;
            justify-content: space-between; } }

    &__range {
        width: 100%;
        height: 30px;
        margin: 20px 0;

        display: flex;
        border-radius: 20px;
        overflow: hidden;

        &_item {
            height: 100%; }

        &_protein {
            background-color: #e64d4d; }
        &_fats {
            background-color: #ffbe7b; }
        &_carb {
            background-color: #fff176; } } }
