.product_preview {
    padding: 10px;
    margin: 5px;
    box-shadow: 0 0 4px 0px rgba(0,0,0,.2);
    cursor: pointer;
    &__title {
        font-size: 18px;
        font-weight: bold;
        margin: 0;
        padding-bottom: 5px; }
    &__content {
        display: flex; }
    &__text {
        user-select: none;
        flex: 1;
        opacity: 0.5;
        font-size: 15px; } }


.product_full {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;

    &__header {
        margin-top: 30px; } }
