body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;

    color: #000;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; }

$c-dark: #444444;
$c-gray: #6a6a6a;
$c-success: #5ddb9f;
$c-gray-light: #bababa;
$btn_main_bg:  linear-gradient(98deg, rgba(2,0,36,1) 0%, rgba(0,169,163,1) 0%, rgba(101,220,157,1) 100%);
$btn_color_bg:  linear-gradient(98deg, rgba(2,0,36,1) 0%, rgba(255,177,101,1) 0%, rgba(255,106,120,1)  100%);
$btn_shadow: 0px 2px 35px 0px rgba(50, 50, 50, 0.3);

p {
    margin: 0;
    padding: 0; }

a {
    cursor: pointer; }
h1,
h2,
h3,
h4 {
    margin: 0;
    padding: 0; }
.edit_eat__header {
    padding-left: 40px; }
.btn {
    background-color: transparent;
    border: none;
    display: inline-block;
    cursor: pointer;
    transition: .3s ease all;

    padding: 10px 80px;

    border-radius: 20px;
    font-size: 18px;

    &_disabled {
        opacity: 0.5; }

    &_transp {
        border: 1px solid $c-gray-light; }

    &_color1 {
        background: $btn_main_bg;
        color: #fff; } }

// range slider
.rangeslider-vertical .rangeslider__handle {
    height: 30px;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center; }

.rangeslider__handle-label {
    user-select: none; }

.rangeslider-vertical .rangeslider__fill,
.rangeslider-horizontal .rangeslider__fill {
    background: $btn_color_bg; }

.rangeslider-vertical,
.slider-vertical,
.slider-group,
.slider {
    height: 100%; }


.rangeslider-horizontal .rangeslider__handle {
    &:after {
        content: none; } }


.rangeslider-horizontal .rangeslider__handle {
    display: flex;
    justify-content: center;
    align-items: center; }

.slider.custom-labels {
    .value {
        font-size: 32px;
        font-weight: bold;
        text-align: center;
        margin-bottom: 63px; } }


.loader {
    width: 100%;
    height: 100%;

    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    &__spin {
        animation: loader 2s infinite ease; }

    &__circle {
        width: 10vmax;
        height: 10vmax;

        background-color: #fff;
        position: relative;
        border-radius: 50%;
        display: block;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            z-index: -1;

            width: 100%;
            height: 100%;
            padding: 0 0 7px 7px;

            display: block;

            background: $btn_color_bg;
            border-radius: 50%; }

        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: -1;

            width: 100%;
            height: 100%;
            padding:  7px 7px 0 0;

            display: block;

            background: $btn_main_bg;
            border-radius: 50%; } } }


@keyframes loader {
    0% {
        transform: rotate(0deg); }
    100% {
        transform: rotate(-360deg); } }

.link_back {
    text-decoration: none;
    color: #000;
    cursor: pointer;
    position: absolute;
    top: 2%;
    left: 2%;

    svg {
        margin-right: 10px; } }


.modal {
    position: fixed;
    box-shadow: 0 0 4px 0px rgba(0,0,0,.2);
    background-color: rgba(0,0,0,.6);
    width: 100%;
    height: 100%;

    div {

        position: absolute;
        z-index: 1;
        background-color: #fff;
        width: 120px;
        padding: 40px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation: slideUp .8s ease; }
    p {
        padding: 3px;
        margin: 5px;
        border: 1px solid;
        cursor: pointer;
        user-select: none; } }

@keyframes slideUp {
    0% {
        opacity: 0;
        transform: translate(-50%, -100%); }
    100% {
        opacity: 1;
        transform: translate(-50%, -50%); } }

.flex {
    display: flex;

    &_column {
        flex-direction: column; } }

.edit_eat {
    &__header {
        background: $btn_main_bg;
        height: 190px; }
    &__title {
        padding-top: 100px;
        color: #fff; } }

.product_full {
    padding: 20px; }


.product_type_preview {
    position: relative;
    cursor: pointer;

    &::before {
        content: "";
        position: absolute;
        transform: translate(0, -50%) rotate(45deg);
        top: 50%;
        right: 20px;
        width: 10px;
        height: 10px;
        border-top: 2px solid #ccc;
        border-right: 2px solid #ccc; } }


.react-sweet-progress-line {
    overflow: hidden; }

.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    box-shadow: 0 0 4px 0px rgba(0,0,0,.8);
    padding: 5px 50px;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    align-items: center; }

.logout {
    display: inline-block;
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: #fff; }


.activate {
    background-color: #2ecc71;
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #ffffff;

    .button-hand {
        button {
            background-color: transparent;
            color: #ffffff;
            border: 3px solid #fff;
            border-radius: 20px;
            padding: 0.8rem 2rem;
            outline: none;
            cursor: pointer;
            position: relative;
            transition: .2s ease-in-out;
            &::before {
                content: "ðŸ‘‡";
                position: absolute;
                font-size: 35px;
                transform: scaleX(-1) rotate(-90deg);
                right: -50px;
                top: 3px;
                animation: up-down 1s infinite; } } }

    &_top {
        margin-bottom: 40px;
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid #ffffff;
        padding-bottom: 40px;
        justify-content: center;
        width: 200px;
        .button-hand {
            margin-top: 10px;
            text-align: center;

            button {
                &::before {
                    content: none; } } } }
    input {
        background-color: transparent;
        border: none;
        color: #ffffff;
        &::placeholder {
            color: #ffffff; }
        border: 2px solid #ffffff;
        margin: 3px;
        border-radius: 20px;
        padding: 3px 10px; } }




@keyframes up-down {
    50% {
        transform: scaleX(-1) rotate(-90deg) translateY(-15px); } }




.profile {
    padding: 5px; }



.filter {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;



    &__item {
        position: relative;
        user-select: none;
        cursor: pointer;
        &_active {
            font-weight: bold;
            &::before {
                content: "â–¼";
                display: inline-block;
                transform: rotate(-180deg); } }

        &_active_up {
            &::before {
                transform: rotate(0deg); } } } }

.user_diet__activate {
    input {
        border: none;
        border: 1px solid #cccccc;
        padding: 5px;
        margin-right: 3px;
        border-radius: 10px; } }



input.error {
    border-color: #f76b6b; }
