.list_eat {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 15px;
    margin-bottom: 15px;
    padding: 10px 0;
    box-shadow: 0 0 3px 2px rgba(0,0,0,.2);
    &__middle {
        width: 50%; }
    &__icon {
        width: 50px;
        height: 50px;
        border-radius: 50%; }

    &__icon.breakfast {
        background-color: #5ddc9c;
        position: relative;

        &::after {
            background: url(../images/cup.svg) no-repeat center / cover;
            content: "";
            position: absolute;

            top: 50%;
            left: 50%;
            width: 60%;
            height: 60%;
            transform: translate(-50%, -50%); } }

    &__icon.dinner {
        background-color: #5ddc9c;
        position: relative;

        &::after {
            background: url(../images/dinner.svg) no-repeat center / cover;
            content: "";
            position: absolute;

            top: 50%;
            left: 50%;
            width: 60%;
            height: 60%;
            transform: translate(-50%, -50%); } }
    &__icon.supper {
        background-color: #5ddc9c;
        position: relative;

        &::after {
            background: url(../images/steak.svg) no-repeat center / cover;
            content: "";
            position: absolute;

            top: 50%;
            left: 50%;
            width: 60%;
            height: 60%;
            transform: translate(-50%, -50%); } }
    &__icon.snack {
        background-color: #5ddc9c;
        position: relative;

        &::after {
            background: url(../images/sandwich.svg) no-repeat center / cover;
            content: "";
            position: absolute;

            top: 50%;
            left: 50%;
            width: 60%;
            height: 60%;
            transform: translate(-50%, -50%); } }

    &__icon.sports {
        background-color: #ffa5002b;
        position: relative;

        &::after {
            background: url(../images/exercise.svg) no-repeat center / cover;
            content: "";
            position: absolute;

            top: 50%;
            left: 50%;
            width: 60%;
            height: 60%;
            transform: translate(-50%, -50%); } }



    &__add {
        border: 1px solid #ccc;
        border-radius: 50%;
        padding: 7px; }
    &__title {
        font-size: 20px; }
    &__progress {
        margin-top: 5px;
        position: relative;
        .react-sweet-progress-symbol {
            display: none; }
        .react-sweet-progress-line-inner {
            height: 15px; } }

    &__cal {
        position: absolute;
        z-index: 1;
        font-size: 13px;
        bottom: 0;
        left: 15px;
        color: #000000;
        opacity: .8; } }
