.diary {
    height: 100%;
    &__head {
        background: $btn_main_bg;
        color: #fff;

        padding-top: 120px;

        .react-sweet-progress-symbol {
            display: none; }

        &_row {
            display: flex;
            justify-content: center;
            padding-bottom: 40px; }

        &_col {
            align-items: center;
            justify-content: center; }

        &_col-2 {
            width: 16.6%; }
        &_col-4 {
            width: 33.33%; }
        &_col-8 {
            width: 66.6%; }

        &_count {
            font-size: 20px;
            padding-top: 5px; }

        &_progress {
            width: 50%; }

        &_circle {
            justify-content: center;
            align-items: center;
            position: relative;

            path {
                &:first-child {
                    stroke: #ccc; } } }

        &_cal {
            justify-content: center;
            align-items: center;
            font-size: 14px;

            position: absolute;
            top: 50%;
            left: 50%;

            transform: translate(-50%, -50%);

            &_remain {
                font-size: 28px; } } } }


